import { ApiError } from '@particle-network/auth-core';
import { Modal } from 'antd';
import { useAuthCoreModal } from '../../context';
import useMessage from './useMessage';

export function formatServerError(error: any): string | undefined {
    if (error && error.error_code) {
        let message = window?.particleI18n?.t?.(`error.server_${error.error_code}`);
        if (message && message.startsWith('error.server_')) {
            if (error?.extra && Array.isArray(error?.extra) && error?.extra.length) {
                message = error?.extra[0];
            } else {
                message = error?.extra ?? error.message;
            }
            message = message.charAt(0).toUpperCase() + message.slice(1);
            return message;
        }
        return message;
    } else {
        return error?.message;
    }
}

export const useError = () => {
    const { authCoreModal } = useAuthCoreModal();

    const message = useMessage();

    /**
     * error handler
     * @param err
     */
    function errorHandle(err: any) {
        console.log('errorHandle: ' + JSON.stringify(err));
        const errorCode = err.error_code ?? err.code;
        // 判断服务器响应
        if (errorCode && typeof errorCode === 'number') {
            if (
                errorCode === ApiError.InvalidCode ||
                errorCode === ApiError.ResendCode ||
                errorCode === ApiError.SecurityAccountFrozen ||
                errorCode === ApiError.WrongPaymentPassword
            ) {
                // Ignore error
            } else {
                const message = formatServerError(err);
                if (message) {
                    Modal.error({
                        title: message,
                        wrapClassName: 'auth-core-modal-error',
                        getContainer: () => {
                            return authCoreModal.rootBody as HTMLElement;
                        },
                    });
                }
            }
        } else if (err.code === 'ECONNABORTED') {
            if (err.message) {
                message.error(err.message);
            } else {
                message.error('Network Error');
            }
        } else {
            switch (err.status) {
                case 401:
                case 403:
                case 404:
                case 500:
                    message.error('Http Error: ' + err.status);
                    break;
                default:
                    if (err.message && typeof err.message === 'string') {
                        message.error(err.message);
                    } else {
                        console.error('http error', err);
                    }
                    break;
            }
        }
    }

    return { errorHandle };
};
