import type { DeviceInfo } from '@particle-network/auth-core';
import { deleteAuthorizationDApp } from '@particle-network/auth-core';
import { Button, Modal } from 'antd';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import Header from '../../../components/header';
import SVGIcon from '../../../components/icon/svg-icon';
import PowerFooter from '../../../components/power-footer';
import { useAuthCoreModal, useCustomNavigate } from '../../../context';
import useMessage from '../../../context/hooks/useMessage';
import styles from './index.less';

interface IProps {
    loginDeviceInfo: DeviceInfo;
}

const DeviceList = (props: IProps) => {
    const { loginDeviceInfo } = props;
    const { t } = useTranslation();
    const navigate = useCustomNavigate();
    const { authCoreModal } = useAuthCoreModal();
    const [visibleModal, setVisibleModal] = useState(false);
    const [deleteLoading, setDeleteLoading] = useState(false);
    const openDeleteModal = () => {
        setVisibleModal(true);
    };
    const message = useMessage();
    const closeDeleteModal = () => {
        setVisibleModal(false);
    };

    return (
        <div className="device-details-wrapper">
            <style>{styles as unknown as string}</style>
            <div className="device-details-container">
                <Header displayBackBtn={true}>{t('new.details')}</Header>
                <div className="scroll-wrapper">
                    <div className="details-list">
                        <div className="item">
                            <div className="left">{t('new.app')}</div>
                            <div className="right">{loginDeviceInfo.project_app_name || '-'}</div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.device')}</div>
                            <div className="right">{loginDeviceInfo.device || '-'}</div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.authorized_time')}</div>
                            <div className="right">{loginDeviceInfo.updated_at || '-'}</div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.login_method')}</div>
                            <div className="right">
                                {(loginDeviceInfo.login_channel || '-').replace('Twitterv1', 'Twitter')}
                            </div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.ip')}</div>
                            <div className="right">{loginDeviceInfo?.location?.ip || '-'}</div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.country')}</div>
                            <div className="right">
                                {loginDeviceInfo?.location?.country_name ||
                                    loginDeviceInfo?.location?.country_code ||
                                    '-'}
                            </div>
                        </div>
                        <div className="item">
                            <div className="left">{t('new.city')}</div>
                            <div className="right">{loginDeviceInfo?.location?.city || '-'}</div>
                        </div>
                    </div>
                </div>
                <div className="btns">
                    <Button
                        className="still-confirm confirm-delete-btn"
                        disabled={loginDeviceInfo.isCurrent}
                        onClick={() => {
                            openDeleteModal();
                        }}
                    >
                        {t('new.delete_access')}
                    </Button>
                </div>
                <Modal
                    className="delete-confirm-modal"
                    open={visibleModal}
                    closable={false}
                    footer={null}
                    centered={true}
                    getContainer={() => {
                        return authCoreModal.rootBody as HTMLDivElement;
                    }}
                >
                    <div className="content-wrap">
                        <div
                            className="close-btn"
                            onClick={() => {
                                closeDeleteModal();
                            }}
                        >
                            <SVGIcon className="circle_close" name="circle_close" />
                        </div>
                        <div className="content-text">
                            {t('new.delete_access_to_n').format(
                                loginDeviceInfo?.project_app_name,
                                loginDeviceInfo?.device
                            )}
                        </div>
                        <div className="btns">
                            <Button
                                className="cancel-btn"
                                onClick={() => {
                                    closeDeleteModal();
                                }}
                            >
                                {t('common.cancel')}
                            </Button>
                            <Button
                                className="still-confirm delete-btn"
                                loading={deleteLoading}
                                onClick={() => {
                                    setDeleteLoading(true);
                                    setTimeout(() => {
                                        deleteAuthorizationDApp(loginDeviceInfo?.project_app_uuid)
                                            .finally(() => {
                                                setDeleteLoading(false);
                                            })
                                            .then((res: boolean) => {
                                                if (res) {
                                                    closeDeleteModal();
                                                    setTimeout(() => {
                                                        navigate(-1);
                                                    }, 500);
                                                }
                                            })
                                            .catch((error: any) => {
                                                message.error(error.message);
                                                console.log('deleteAuthorization Error', error);
                                            });
                                    }, 500);
                                }}
                            >
                                {t('new.delete')}
                            </Button>
                        </div>
                    </div>
                </Modal>

                <PowerFooter className="footer-box-v2" />
            </div>
        </div>
    );
};

export default DeviceList;
