import { useTranslation } from 'react-i18next';
import useMessage from '../..//context/hooks/useMessage';
import Header from '../../components/header';
import SVGIcon from '../../components/icon/svg-icon';
import PowerFooter from '../../components/power-footer';
import { useCustomNavigate, useParticleAuth } from '../../context';
import { PageType } from '../account/accountVerify/config';

import email from '../../common/images/email_icon.png';
import facebook from '../../common/images/facebook_icon.png';
import google from '../../common/images/google_icon.png';
import phone from '../../common/images/phone_icon.png';
import twitter from '../../common/images/twitter_icon.png';

import styles from './index.less';

