import { ApiError, verifyPaymentPassword } from '@particle-network/auth-core';
import { useRequest } from 'ahooks';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import ParticleDrawer from '../../../components/particle-drawer';
import { useAuthCoreModal, useParticleAuth } from '../../../context';
import ParticleKeywords from '../ParticleKeywords';
import styles from './index.less';
export interface PaymentVerify {
    visible: boolean;
    onVerifyCompleted?: (password: string) => void;
    onVerifyFailed?: (message: string) => void;
    type?: 'close' | 'verify';
}

//支付时验证
const PaymentVerifyModal = ({ props }: { props: PaymentVerify }) => {
    const { setPaymentVerify, setWrongPassword } = useParticleAuth();
    const { t } = useTranslation();

    const [pwd, setPwd] = useState('');
    const { authCoreModal } = useAuthCoreModal();
    useEffect(() => {
        if (props.visible) {
            setPwd('');
        }
    }, [props.visible]);

    const { loading: verifyLoading, run: runVerify } = useRequest(verifyPaymentPassword, {
        manual: true,
        onSuccess: (result, params) => {
            setPaymentVerify({ visible: false });
            props.onVerifyCompleted?.(pwd);
        },
        onError: (error: any) => {
            console.log('verify password error', error);
            setPwd('');
            if (error?.error_code === ApiError.WrongPaymentPassword) {
                setWrongPassword({ visible: true });
            } else if (error?.error_code === ApiError.SecurityAccountFrozen) {
                const seconds = error.extra.seconds || 0;
                setWrongPassword({
                    visible: true,
                    accountFrozen: {
                        seconds: seconds,
                    },
                });
            }
        },
    });

    const closeVerify = () => {
        if (verifyLoading) return false;
        setPaymentVerify({ visible: false });
        props.onVerifyFailed?.(t('common.cancel'));
    };

    const checkInputPwd = (value: string) => {
        setPwd(value);
        if (value.length === 6) {
            runVerify(value);
        }
    };

    return (
        <>
            <style>{styles as unknown as string}</style>
            <ParticleDrawer
                visible={props.visible}
                placement="bottom"
                height={421}
                closable={false}
                maskClosable={false}
                onClose={closeVerify}
                className="payment-password-drawer"
                title={
                    (props.type === 'close'
                        ? t('account.close_payment_password')
                        : t('account.payment_password')) as string
                }
                forceRender={true}
                getContainer={() => {
                    return authCoreModal.rootBody as HTMLElement;
                }}
            >
                <div className="content payment-verify-content">
                    <ParticleKeywords onChange={checkInputPwd} value={pwd} keyboardInvisible={verifyLoading} />
                </div>
            </ParticleDrawer>
        </>
    );
};

export default PaymentVerifyModal;
