import { Button, Modal } from 'antd';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useAuthCoreModal } from '..';
import { useEVMTransaction } from '../../pages/sign/evmContextProvider';
import { useAuthCore } from './useAuthCore';
import { useEthereum } from './useEthereum';

const usePending = () => {
    const { t } = useTranslation();
    const { transactionData, fetchPendingTransactions } = useEVMTransaction();

    const { address } = useEthereum();
    const { openWallet } = useAuthCore();
    const { authCoreModal } = useAuthCoreModal();

    const pendingModalRef = React.useRef<any>();

    const { authCoreModalClose } = useAuthCoreModal();

    const checkPending = async (approveSign: any, pendingConfirm: boolean) => {
        let pendingTransactions = [];

        if (
            transactionData &&
            transactionData.action !== 'cancel' &&
            transactionData.action !== 'speedup' &&
            !pendingConfirm
        ) {
            pendingTransactions = await fetchPendingTransactions(address as string);
        }

        if (
            transactionData &&
            transactionData.action !== 'cancel' &&
            transactionData.action !== 'speedup' &&
            !pendingConfirm &&
            (pendingTransactions?.length >= 3 ||
                !!pendingTransactions.find((item: any) => {
                    const now = new Date().getTime();
                    const timestamp = new Date(item.timestamp * 1000).getTime();
                    return now - timestamp > 30 * 60 * 1000;
                }))
        ) {
            // 当该地址有超过3笔交易正在Pending或一笔交易timestamp超过30分钟未完成时，用户再次确认交易时将弹出此弹窗
            console.log('pendingTransactions', pendingTransactions);

            pendingModalRef.current = Modal.warning({
                className: 'pending-warning-modal  auth-core-modal-warning',
                content: (
                    <div className="content-wrap">
                        <div className="content">{t('new.transaction_pending_v2')}</div>
                        <div className="footer-btns">
                            <Button
                                className="cancel-btn continue-btn"
                                disabled={pendingTransactions?.length >= 10}
                                type="default"
                                onClick={() => {
                                    approveSign(true);
                                    pendingModalRef.current.destroy();
                                }}
                            >
                                {t('new.continue')}
                            </Button>
                            <Button
                                className="process-now-btn"
                                type="primary"
                                onClick={() => {
                                    // 取消签名，跳转到wallet
                                    console.log('>>>', '取消签名，跳转到wallet');

                                    pendingModalRef.current.destroy();
                                    authCoreModalClose();

                                    setTimeout(() => {
                                        openWallet({
                                            pathName: '/tokenDetail.html',
                                            query: {
                                                tokenAddress: 'native',
                                                pageMode: 'Simple',
                                            },
                                            topMenuType: 'close',
                                        });
                                    }, 30);
                                }}
                            >
                                {t('new.process_now')}
                            </Button>
                        </div>
                    </div>
                ),
                closable: true,
                maskClosable: false,
                getContainer: () => {
                    return (authCoreModal.rootBody as HTMLElement).querySelector('.info-sign') as HTMLElement;
                },
            });
            return true;
        }

        return false;
    };

    return {
        checkPending,
    };
};

export default usePending;
