import { useRequest } from 'ahooks';
import { Button } from 'antd';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useCustomNavigate } from '../../../context';
import ParticleKeywords from '../ParticleKeywords/index';

import { updatePaymentPassword } from '@particle-network/auth-core';
import React from 'react';
import SVGIcon from '../../../components/icon/svg-icon';
import { useError } from '../../../context/hooks/useError';
import { PageType } from '../accountVerify/config';
import styles from './index.less';

const SetPaymentPassword = (props: any) => {
    const navigate = useCustomNavigate();
    const { t } = useTranslation();
    const state = props;
    // 1. loading  3. password  4,success 5. check 6. code 7  confirm password
    type StepType = 'loading' | 'password' | 'success' | 'check' | 'code' | 'confirm';
    const [step, setStep] = useState<StepType>('loading');
    const [codeVal, setCodeVal] = useState('');
    const [password, setPassword] = useState('');
    const [passwordMistake, setPasswordMistake] = useState<boolean>();
    const [passwordMatched, setPasswordMatched] = useState<boolean>(false);
    const { errorHandle } = useError();

    const { loading: updateLoading, run: runUpdate } = useRequest(updatePaymentPassword, {
        manual: true,
        onSuccess: () => {
            navigate(-1);
        },
        onError: (error: any) => {
            console.log('update password error', error);
            errorHandle(error);
            if (error?.error_code === 50104) {
                setCodeVal('');
                setPassword('');
                setPasswordMistake(false);
                setStep('password');
            }
        },
    });

    useEffect(() => {
        setStep('password');
    }, []);

    const checkPassword = (value: string) => {
        if (value.length === 6) {
            if (value === password) {
                setCodeVal(value);
                setPasswordMatched(true);
            } else {
                setCodeVal('');
                setPassword('');
                setPasswordMistake(true);
                setStep('password');
                setPasswordMatched(false);
            }
        } else {
            setCodeVal(value);
            setPasswordMatched(false);
        }
    };

    const submitPassword = () => {
        if ((state as any)?.account) {
            //reset password
            navigate('/account/verify', {
                state: {
                    account: (state as any)?.account,
                    password: codeVal,
                    pageType: PageType.ResetPaymentPassword,
                },
            });
        } else if ((state as any)?.oldPassword) {
            //change password
            const oldPassword = (state as any)?.oldPassword;
            const password = codeVal;
            runUpdate({
                password: password,
                oldPassword: oldPassword,
            });
        }
    };

    if (step === 'confirm') {
        return (
            <div className="set-password-container">
                <style>{styles as unknown as string}</style>
                <SVGIcon className="icon-navigation-back" name="circle_back" onClick={() => navigate(-1)} />
                <h2 className="payment-title">{t('account.set_payment_password')}</h2>
                <p className="payment-desc-1">{t('account.re_enter_confirm')}</p>
                <div className="keyboard-container">
                    <ParticleKeywords onChange={checkPassword} value={codeVal}></ParticleKeywords>
                </div>
                <div className="payment-buttons2">
                    <Button
                        className="payment-main-button2"
                        disabled={!passwordMatched}
                        loading={(state as any)?.oldPassword ? updateLoading : false}
                        onClick={submitPassword}
                    >
                        {t('account.done')}
                    </Button>
                </div>
            </div>
        );
    }

    return (
        <>
            <style>{styles as unknown as string}</style>
            <div className="set-password-container">
                <SVGIcon className="icon-navigation-back" name="circle_back" onClick={() => navigate(-1)} />
                <h2 className="payment-title">{t('account.set_payment_password')}</h2>
                <p className="payment-desc-1">{t('account.set_payment_password_tip')}</p>
                <div className="keyboard-container">
                    <ParticleKeywords
                        onChange={(e) => {
                            setPassword(e);
                            if (e.length > 5) {
                                setPasswordMistake(false);
                                setPasswordMatched(false);
                                setStep('confirm');
                            }
                        }}
                        value={password}
                    ></ParticleKeywords>
                </div>

                {passwordMistake && <div className="password-mistake">{t('account.password_do_not_match')}</div>}
            </div>
        </>
    );
};

export default SetPaymentPassword;
