import { ApiError, getCloudflareTurnstileResponse, isApiError, type CaptchaRequest } from '@particle-network/auth-core';
import { useInterval } from 'ahooks';
import { Button } from 'antd';
import throttle from 'lodash/throttle.js';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import SVGIcon from '../../../components/icon/svg-icon';
import AuthCode from '../../../components/react-auth-code-input';
import { useParticleAuth } from '../../../context';
import { useCustomize } from '../../../context/hooks/useCustomize';
import useLogin from '../../../context/hooks/useLogin';
import useMessage from '../../../context/hooks/useMessage';
import styles from './captcha-input.less';

function CaptchaInput(props: any) {
    const { sendCaptcha, setShowInputCaptcha, account, loginWithAccount, chain } = props;

    const [loading, setLoading] = useState(false);

    const { themeType, language } = useCustomize();
    const [codeVal, setCodeVal] = useState<string>('');
    const { loginHandler } = useLogin();
    const [noInput, setNoInput] = useState(false);
    const [wrongInput, setWrongInput] = useState(false);
    const [invalidInput, setInvalidInput] = useState(false);
    const [resendTip, setResendTip] = useState(false);
    const [confirmBtn, setConfirmBtn] = useState(false);
    const { authCoreModal } = useParticleAuth();
    const { t } = useTranslation();
    const message = useMessage();
    const codeInput = useRef<any>(null);
    const codePattern = /^\d{6}$/;
    const [interval, setInterval] = useState<number | undefined>(1000);

    const [countdown, setCountdown] = useState(60);

    useInterval(() => {
        if (countdown > 0) {
            const result = countdown - 1;
            setCountdown(result);
            if (result === 0) {
                setInterval(undefined);
            }
        }
    }, interval);

    const startCountdown = () => {
        setCountdown(60);
        setInterval(1000);
    };

    const getCaptcha = () => {
        if (account.includes('@')) {
            sendCaptchaImpl({ email: account });
        } else {
            getCloudflareTurnstileResponse({
                theme: themeType,
                language,
                getContainer: () => {
                    return authCoreModal.rootBody as HTMLElement;
                },
            })
                .then((token) => {
                    sendCaptchaImpl({
                        phone: account,
                        // eslint-disable-next-line camelcase
                        cf_turnstile_response: token,
                    });
                })
                .catch(() => {
                    message.error(t('error.server_20112'));
                });
        }
    };

    const sendCaptchaImpl = (param: CaptchaRequest) => {
        sendCaptcha(param)
            .then(() => {
                console.log('send captcha success');
                startCountdown();
                setConfirmBtn(false);
                setResendTip(false);
            })
            .catch(() => {
                setCountdown(0);
                setInterval(undefined);
            });
    };

    const codeChange = (value: any) => {
        setWrongInput(false);
        // const code = value.join("");
        const code = value;
        const patt = /^[0-9]+$/g;
        if (code.length === 0) {
            setWrongInput(false);
            setNoInput(true);
        } else {
            if (!patt.test(code)) {
                setNoInput(false);
                setWrongInput(true);
            } else {
                setNoInput(false);
                setWrongInput(false);
            }
        }
    };

    const login = (code?: string) => {
        setNoInput(false);
        setWrongInput(false);
        setInvalidInput(false);
        code = code || codeVal;
        console.log(`code --> ${code}`);
        if (noInput || wrongInput) {
            return;
        }
        if (codePattern.test(code)) {
            throttleLoginImpl(code);
        } else {
            if ((code.length > 0 && code.length < 6) || !codePattern.test(code)) {
                setWrongInput(true);
                setNoInput(false);
            }
            if (code.length === 0) {
                setWrongInput(false);
                setNoInput(true);
            }
        }
    };

    const loginImpl = async (code: string) => {
        if (resendTip) {
            return;
        }
        setLoading(true);
        let data;
        if (account.includes('@')) {
            data = {
                code: code,
                email: account,
                chain,
            };
        } else {
            data = {
                code: code,
                phone: account,
                chain,
            };
        }
        try {
            await loginHandler(data, false);
        } catch (error) {
            handleLoginError(error);
        }
    };

    const handleLoginError = (error: any) => {
        console.log('login error', error);
        setLoading(false);
        if (isApiError(error)) {
            const errorCode = error.error_code;
            if (errorCode === ApiError.InvalidCode) {
                setInvalidInput(true);
            } else if (errorCode === ApiError.ResendCode) {
                setResendTip(true);
                setConfirmBtn(true);
            }
        }
    };

    const onCodeInputChange = (codeArray: string) => {
        codeChange(codeArray);
        setCodeVal(codeArray);
        setNoInput(false);
        setWrongInput(false);
        setInvalidInput(false);
        setResendTip(false);
        if (codePattern.test(codeArray)) {
            throttleLoginImpl(codeArray);
        }
    };

    const throttleLoginImpl = useCallback(
        throttle(
            (code) => {
                loginImpl(code);
            },
            1000,
            {
                leading: true,
                trailing: false,
            }
        ),
        []
    );

    const handleCodeInputFocus = () => {
        const elements = document.getElementsByClassName('input-code-item');
        //@ts-ignore
        elements[Math.min(codeVal.length, 5)]?.focus();
    };

    const verifyCodeErrorMessage = useMemo(() => {
        const error = [
            noInput ? t('login.enter_captcha') : '',
            wrongInput ? t('login.captcha_input_error') : '',
            invalidInput ? t('login.invalid_code') : '',
            resendTip ? t('login.please_send_again') : '',
        ]
            .map((val) => val.trim())
            .filter((val) => !!val)
            .join(',');

        return error.includes(',') ? error + '.' : error;
    }, [noInput, wrongInput, invalidInput, resendTip]);

    useEffect(() => {
        const elements = document.getElementsByClassName('react-input-code');
        const contextmenuEvent = (e: any) => e.preventDefault();
        elements[0]?.addEventListener('contextmenu', contextmenuEvent);
        return () => {
            elements[0]?.removeEventListener('contextmenu', contextmenuEvent);
        };
    }, []);

    return (
        <div className="captcha-box">
            <style>{styles as unknown as string}</style>
            <div className="captcha-form">
                <div className="input-code-content">
                    <div onClick={handleCodeInputFocus}>
                        <AuthCode
                            containerClassName="react-input-code"
                            inputClassName="input-code-item"
                            allowedCharacters="numeric"
                            length={6}
                            ref={codeInput}
                            placeholder={' '}
                            onChange={onCodeInputChange}
                        />
                    </div>
                </div>
            </div>
            <div className="error-tip">{verifyCodeErrorMessage}</div>

            <div className="btn-box">
                <Button
                    type="primary"
                    htmlType="submit"
                    className="primary-antd-btn submit-btn"
                    onClick={() => login()}
                    disabled={confirmBtn}
                    loading={loading}
                >
                    {t('common.confirm')}
                </Button>

                <Button className="get-btn" disabled={countdown > 0} type="link" onClick={getCaptcha}>
                    {t('login.send_again')} {countdown > 0 ? `(${countdown}s)` : ''}
                </Button>

                {!loginWithAccount && (
                    <div
                        className="back-box"
                        onClick={() => {
                            setShowInputCaptcha(false);
                        }}
                    >
                        <SVGIcon className="arrow1-icon" name="arrow1_icon" />
                        <span>{t('login.back')}</span>
                    </div>
                )}
            </div>
        </div>
    );
}

export default CaptchaInput;
