import { LoadingOutlined } from '@ant-design/icons';
import {
    AuthType,
    getConnectCaptcha,
    isNullish,
    requireNotNullish,
    type CaptchaRequest,
    type ConnectParam,
    type PhoneCaptchaRequest,
    type SocialAuthType,
} from '@particle-network/auth-core';
import { Image } from 'antd';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import logoImg from '../../common/images/logo.png';
import PowerFooter from '../../components/power-footer';
import { useParticleAuth } from '../../context';
import useLogin from '../../context/hooks/useLogin';
import { type ConnectOptions } from '../../types/connectOptions';
import { EmailRegExp, isValidE164PhoneNumber } from '../../utils';
import CaptchaInput from './components/captcha-input';
import EmailLogin from './components/email-login';
import loginStyles from './components/login.less';
import PhoneLogin from './components/phone-login';
import SocialLogin from './components/social-login';
import styles from './index.less';

function Login(props?: ConnectOptions) {
    // @ts-ignore
    const { email, phone, code, jwt, socialType, nonce, prompt, authorization, chain } = props || {};

    const { modalOptions, setLoginAuthorization } = useParticleAuth();
    const { loginHandler, socialAuthLogin } = useLogin();

    const getDefaultLoginType = () => {
        const indexPhone = modalOptions.authTypes?.indexOf(AuthType.phone) ?? -1;
        const indexEmail = modalOptions.authTypes?.indexOf(AuthType.email) ?? -1;
        if (socialType) {
            return socialType;
        } else if (
            (!isNullish(phone) && (indexPhone >= 0 || !modalOptions.authTypes?.length)) ||
            (!email &&
                ((indexPhone >= 0 && indexEmail < 0) ||
                    (indexPhone >= 0 && indexEmail >= 0 && indexPhone < indexEmail)))
        ) {
            return AuthType.phone;
        } else {
            return AuthType.email;
        }
    };

    const [loginType, setLoginType] = useState<AuthType>(getDefaultLoginType()); // email or phone

    const { t } = useTranslation();

    const [account, setAccount] = useState<string | undefined>('');

    const [showInputCaptcha, setShowInputCaptcha] = useState(false);

    const [loginWithAccount, setLoginWithAccount] = useState(false);

    const [supportAuthTypes, setSupportAuthTypes] = useState<AuthType[]>(modalOptions.authTypes || []);

    const displayInputCaptcha = (visible: boolean) => {
        setShowInputCaptcha(visible);
    };

    useEffect(() => {
        document.documentElement.setAttribute('data-login-type', loginType || '');
    }, [loginType]);

    useEffect(() => {
        if (!loginWithAccount) {
            const allTypes = Object.values(AuthType).filter((authType) => authType !== AuthType.jwt);
            if (!modalOptions.authTypes) {
                setSupportAuthTypes(allTypes);
            } else {
                const types = modalOptions.authTypes.filter(
                    (authType) => authType !== AuthType.jwt && allTypes.includes(authType)
                );
                if (socialType && !types.includes(socialType)) {
                    types.push(socialType);
                }
                if (!types.includes(AuthType.email) && !types.includes(AuthType.phone) && types.length > 0) {
                    types.unshift(AuthType.email);
                }
                if (types.length === 1 && (types[0] === AuthType.email || types[0] === AuthType.phone)) {
                    //clear all
                    types.length = 0;
                }
                setSupportAuthTypes(types);
            }
        } else {
            setSupportAuthTypes([]);
        }
    }, [loginWithAccount, modalOptions.authTypes]);

    useEffect(() => {
        if (authorization) {
            setLoginAuthorization({ authorization, chain: requireNotNullish(chain) });
        } else {
            setLoginAuthorization(undefined);
        }

        loginDirectly();
    }, []);

    //发送验证码
    const sendCaptcha = async (data: CaptchaRequest): Promise<boolean> => {
        setAccount('email' in data && data.email ? data.email : (data as PhoneCaptchaRequest).phone);
        const result = await getConnectCaptcha(data).then((result) => {
            console.log(`request captcha success: ${result}`);
            return result;
        });
        return result;
    };

    const isValidCode = () => {
        return code && code.match(/^\d{6}$/);
    };

    // login directly
    const loginDirectly = () => {
        if (email && loginType === AuthType.email) {
            if (EmailRegExp.test(email)) {
                if (isValidCode()) {
                    loginImpl({ email, code, chain });
                } else {
                    setLoginWithAccount(true);
                    setAccount(email);
                }
            }
        } else if (phone && loginType === AuthType.phone) {
            const e164Phone = isValidE164PhoneNumber(phone);
            if (e164Phone) {
                if (isValidCode()) {
                    loginImpl({ phone: e164Phone, code, chain });
                } else {
                    setLoginWithAccount(true);
                    setAccount(e164Phone);
                }
            }
        } else if (jwt) {
            setLoginType(AuthType.jwt);
            setLoginWithAccount(true);
            loginImpl({ jwt, chain });
        } else if (socialType) {
            // social login
            if (code && nonce) {
                loginImpl({ socialType, code, nonce, chain });
            } else {
                // redirect to get code.
                thirdpartyAuthLogin(socialType);
            }
        }
    };

    const loginImpl = useCallback(
        (data: ConnectParam) => {
            loginHandler(data);
        },
        [loginHandler]
    );

    const thirdpartyAuthLogin = (authType: AuthType) => {
        setLoginType(authType);
        socialAuthLogin({
            socialType: authType as SocialAuthType,
            prompt,
            authorization,
            chain,
        });
    };

    const loginContent = () => {
        if (showInputCaptcha) {
            return (
                <CaptchaInput
                    sendCaptcha={sendCaptcha}
                    setShowInputCaptcha={displayInputCaptcha}
                    account={account}
                    loginWithAccount={loginWithAccount}
                    chain={chain}
                />
            );
        } else if (loginType === AuthType.email && !isValidCode()) {
            return (
                <EmailLogin
                    sendCaptcha={sendCaptcha}
                    setShowInputCaptcha={displayInputCaptcha}
                    setLoginType={changeLoginType}
                    account={email || account}
                    supportAuthTypes={supportAuthTypes}
                    loginWithAccount={!!email || loginWithAccount}
                />
            );
        } else if (loginType === AuthType.phone && !isValidCode()) {
            return (
                <PhoneLogin
                    sendCaptcha={sendCaptcha}
                    setShowInputCaptcha={displayInputCaptcha}
                    setLoginType={changeLoginType}
                    account={phone || account}
                    supportAuthTypes={supportAuthTypes}
                    loginWithAccount={!!phone || loginWithAccount}
                />
            );
        }
    };

    const changeLoginType = (type: AuthType) => {
        setLoginType(type);
    };

    const authTypeDisplayed = (type: AuthType): string => {
        if (type === AuthType.jwt) {
            return 'JWT';
        } else {
            return type.titleCase();
        }
    };

    const loggingHint = (loginType: AuthType): string => {
        if (loginType === AuthType.jwt) {
            return t('login.create_wallet');
        } else {
            return t('login.social_login').format(authTypeDisplayed(loginType));
        }
    };

    const projectLogo: string = useMemo(() => {
        let img = modalOptions.customStyle?.logo;
        if (!img) {
            img = logoImg;
        }
        return (img || '') as string;
    }, [modalOptions.customStyle?.logo]);

    return (
        <div className="login-container-box">
            <style>{loginStyles as unknown as string}</style>
            <style>{styles as unknown as string}</style>
            <div className="wrap">
                <div className="login-logo">
                    <Image
                        className="product-logo"
                        preview={false}
                        height={64}
                        src={projectLogo}
                        key={projectLogo}
                        placeholder={
                            <div className="placeholder-wrap">
                                <LoadingOutlined style={{ fontSize: 24 }} spin />
                            </div>
                        }
                        fallback={logoImg}
                        onError={(e) => {
                            e.currentTarget.src = logoImg;
                        }}
                    />
                </div>
                <div className="login-header0">
                    {modalOptions.customStyle?.projectName ?? t('common.particle_auth')}
                </div>

                {showInputCaptcha ? (
                    <div className="login-des">{account}</div>
                ) : (
                    <div className="login-des">
                        {(loginType === AuthType.email || loginType === AuthType.phone) && !isValidCode() ? (
                            modalOptions.customStyle?.subtitle ?? t('login.to_continue').format('App')
                        ) : (
                            <div className="third-party-des">{loggingHint(loginType)}</div>
                        )}
                    </div>
                )}

                {loginContent()}

                {!showInputCaptcha &&
                    !loginWithAccount &&
                    (loginType === AuthType.email || loginType === AuthType.phone) &&
                    !isValidCode() && (
                        <SocialLogin
                            supportAuthTypes={supportAuthTypes}
                            preferredAuthType={loginType}
                            onPreferredAuthTypeChange={setLoginType}
                            thirdpartyAuthLogin={thirdpartyAuthLogin}
                        />
                    )}
            </div>
            <PowerFooter className="footer-box-v2" />
        </div>
    );
}

export default Login;
