import { bindLoginAccount } from '@particle-network/auth-core';
import { Button } from 'antd';
import React, { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Header from '../../../components/header';
import SVGIcon from '../../../components/icon/svg-icon';
import { useCustomNavigate } from '../../../context';
import { useUserInfo } from '../../../context/hooks';
import useMessage from '../../../context/hooks/useMessage';
import { getAccountList } from '../../loginAccount';
import styles from './index.less';

const LoginAccountBindLoading = (props: any) => {
    const state = props;
    const { authType = 'google', verifyToken, code } = state;
    const navigate = useCustomNavigate();
    const { t } = useTranslation();
    const [loading, setLoading] = useState(true);
    const { userInfo } = useUserInfo();
    const message = useMessage();
    const accountList = useMemo(() => {
        return getAccountList({ userInfo, t });
    }, [userInfo]);

    const authItem = useMemo(() => {
        const item = (accountList.find((item) => item.type.replace(/v1$/, '') == authType) || {}) as any;
        return {
            ...item,
        };
    }, [accountList, state]);

    useEffect(() => {
        if (authType && verifyToken) {
            bindLoginAccount({
                provider: authType,
                // eslint-disable-next-line camelcase
                thirdparty_code: code,
                // eslint-disable-next-line camelcase
                security_account_verify_token: verifyToken,
                version: 'v2',
            })
                .then((res) => {
                    setLoading(false);
                })
                .catch((error) => {
                    message.error(error.message);
                    setTimeout(() => {
                        navigate('/account/security', { replace: true });
                    });
                });
        }
    }, [authType, verifyToken]);

    return (
        <div className="account-bind-container">
            <style>{styles as unknown as string}</style>
            <Header displayBackBtn={true}></Header>
            <div className="particle-connect-form-contaier center-center flex-column">
                {loading ? (
                    <div className="particle-loading">
                        <SVGIcon name={authItem.type + '_icon'} className="logo-img logo-img-2" />
                        <p>
                            <img
                                className="particle-loading-img"
                                width={30}
                                height={30}
                                src={require('../../../common/images/loading.png').default}
                                alt=""
                            />
                        </p>
                    </div>
                ) : (
                    <div className="result-content resultsuccess">
                        <img src={authItem?.icon} alt="" />
                        <div className="info">Binding succeeded！</div>
                        <div className="back">
                            <Button
                                type="primary"
                                onClick={() => {
                                    navigate('/account/security', { replace: true });
                                }}
                            >
                                Back
                            </Button>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

export default LoginAccountBindLoading;
